import {AfterViewInit, ChangeDetectorRef, Component, Input, OnDestroy, OnInit, ViewEncapsulation} from "@angular/core";
import {ServerService} from "../../services/server-service";

import {NavBarService} from "../../services/nav-bar.service";
import {ActivatedRoute, Router} from "@angular/router";
import {Subscription} from "rxjs/Subscription";
import {FormControl, FormGroup} from "@angular/forms";
import {AppInteractionService} from "../../services/app-interaction.service";
import {AppPagerService} from "../../services/app-pager.service";


@Component({
  selector: 'app-parties',
  templateUrl: './parties.component.html',
  styleUrls: ['./parties.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PartiesComponent implements OnInit, AfterViewInit, OnDestroy {
  parties: any[];
  navBarSubscription: Subscription;
  createCustomer = AppInteractionService.CREATE_CUSTOMER;
  googleMapsClient: any;
  originalParties: any[];
  pager:any = {};
  pagedParties:any[];
  searchFormGroup: FormGroup;

  constructor(private serverService: ServerService,
              private navBarService: NavBarService,
              private changeDetectorRef: ChangeDetectorRef,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private appPagerService: AppPagerService) {

  }


  ngOnInit() {
    console.log(this.constructor.name);
    this.searchFormGroup = new FormGroup({
      'search': new FormControl(null)
    });
    this.serverService.getAllParties()
      .subscribe((response: any) => {
        this.parties = response._embedded.parties.slice();
        this.originalParties = response._embedded.parties.slice();
        this.prepareSearchInput();
        this.setPage(1);
      }, (error) => {
        console.log(error);
      });
    this.navBarSubscription = this.navBarService.getNavBarSubject().subscribe((value) => {
      // the timings of the nav bar css transition should be considered
      console.log("sizeColumnsToFit");
    });
  }

  setPage(page:number) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    // get pager object from service
    this.pager = this.appPagerService.getPager(this.originalParties.length, page);

    // get current page of items
    this.pagedParties = this.originalParties.slice(this.pager.startIndex, this.pager.endIndex + 1);
  }

  newCustomer() {

  }

  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onRowClicked(uuid:string) {

      let href = uuid;
      this.router.navigate([href, 'main'], {relativeTo: this.activatedRoute})
        .then((value) => {
          console.log(value);
        }).catch((error) => {
        console.log(error)
      });
  }

  onCellDoubleClicked($event) {

  }

  prepareSearchInput() {
    this.searchFormGroup.get('search')
      .valueChanges.subscribe((value) => {
      console.log(value.trim().length);
      if (value.trim().length > 0) {
        this.pagedParties.length = 0;
        for (let i = 0; i < this.originalParties.length; i++) {
          if (this.originalParties[i].name.toLowerCase().search(value.toLowerCase()) > -1) {
            this.pagedParties.push(this.originalParties[i]);
          }
        }
        for (let i = 0; i < this.originalParties.length; i++) {
          if (this.originalParties[i].physicalAddressStr && this.originalParties[i].physicalAddressStr.toLowerCase().search(value.toLowerCase())  > -1) {
            this.pagedParties.push(this.originalParties[i]);
          }
        }
        for (let i = 0; i < this.originalParties.length; i++) {
          if (this.originalParties[i].email && this.originalParties[i].email.toLowerCase().search(value.toLowerCase())  > -1) {
            this.pagedParties.push(this.originalParties[i]);
          }
        }
      }
      else {
        console.log(this.originalParties);
        this.pagedParties = this.originalParties.slice();
        this.setPage(this.pager.currentPage);
      }
      console.log(value);
    }, (error) => {
      console.log(error);
    })
  }

  ngAfterViewInit() {

  }

  ngOnDestroy() {
    this.navBarSubscription.unsubscribe();
  }

}



