import { Injectable } from '@angular/core';
import {Subject} from "rxjs/Subject";

@Injectable()
export class AppInteractionService {

  static readonly QUOTE_REQUEST = "Quote Request";
  static readonly SERVICE_REQUEST = "Service Request";
  static readonly TYPE_MAIN = 'TYPE_MAIN';
  static readonly TYPE_JOBS = 'TYPE_JOBS';
  static readonly TYPE_BILLS = 'TYPE_BILLS';
  static readonly TYPE_INVOICES = 'TYPE_INVOICES';
  static readonly TYPE_COMMENTS = 'TYPE_COMMENTS';
  static readonly TYPE_SCHEDULES ='TYPE_SCHEDULES';
  static readonly TYPE_FIELDWORKS ='TYPE_FIELDWORKS';
  static readonly CREATE_CUSTOMER ='Create Customer';

  appSource = new Subject<any>();
  appSourceObservable = this.appSource.asObservable();

  childComponentSubject = new Subject<any>();
  childComponentObservable = this.childComponentSubject.asObservable();

  constructor() { }

  announceRequestDetail(data) {
    console.log(data);
    this.appSource.next(data);
  }

  requestApplicationComponent(data) {
    setTimeout(() => {
      this.childComponentSubject.next(data);
    }, 100);
  }

}



