import {AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation} from '@angular/core';
import {Subscription} from "rxjs/Subscription";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {ServerService} from "../../../../services/server-service";
import {Http} from "@angular/http";
import {ActivatedRoute, Router} from "@angular/router";
import {JobService} from "../../../../services/job.service";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";

@Component({
  selector: 'app-job-timeline',
  templateUrl: './job-timeline.component.html',
  styleUrls: ['./job-timeline.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class JobTimelineComponent implements OnInit, OnDestroy, AfterViewInit {
  jobStatusEntries: any = null;
  newComment: boolean = false;
  userCommentInput: string;
  paramsReceived: any;
  jobServiceSubscription: Subscription;
  path: string = null;
  commentForm: FormGroup;
  partyId: string = null;
  job:any = null;
  jobStatuses:any;

  constructor(private serverService: ServerService, private http: Http,
              private activatedRoute: ActivatedRoute, private changeDetectorRef: ChangeDetectorRef,
              private  router: Router, private jobService: JobService,
              private ngbModal: NgbModal) {
  }

  ngOnInit() {
    this.jobServiceSubscription = this.jobService.jobSourceObservable
      .subscribe((data) => {
        if (data && data.id && data.type === JobService.TYPE_TIMELINE) {
          this.paramsReceived = data.id;
          this.path = "http://localhost:8081/api/jobs/" + this.paramsReceived + "/statusEntries";
          this.serverService.getRequestByJobId(this.paramsReceived)
            .subscribe((response) => {
              this.job = response;
              this.setJobStatuses();
              this.setJobStatusEntries();
            }, (error) => {
              console.log(error);
            });
        }
      });
  }

  ngAfterViewInit() {
    this.jobService.announceParentComponent({type: JobService.TYPE_TIMELINE});
  }

  setJobStatusEntries() {
    this.serverService.getRequestByPath(this.path)
      .subscribe(
        (jobStatuses: any) => {
          console.log(jobStatuses);
          this.jobStatusEntries = jobStatuses._embedded.jobStatusEntries;
          this.formatDate();
        },
        (error) => {
          console.log(error);
        });
  }

  setJobStatuses() {
    let jobType = this.job.type;
    let pathRequest = "http://localhost:8081/api/jobTypes/" + jobType + "/jobStatuses";
    this.serverService.getRequestByPath(pathRequest)
      .subscribe((response) => {
        this.jobStatuses = response._embedded.jobStatuses;
        console.log(response);
      }, (error) => {
        console.log(error)
      });
  }


  formatDate() {
    this.jobStatusEntries.forEach((data) => {
      let date = data.entryDtm;
      let formattedDate = new Date(date);
      data.entryDateTimeMonth = formattedDate.toDateString();
    });
  }

  getFormattedCssString(value:string) {

      value = value.toLowerCase().trim().replace(/\s+/g, '-');
      console.log(value);
      return value;
  }



  ngOnDestroy() {

  }
}



