import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ServerService} from "../../services/server-service";

@Component({
  selector: 'app-staff',
  templateUrl: './staff.component.html',
  styleUrls: ['./staff.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class StaffComponent implements OnInit {

  staff:any[];

  constructor(private serverService: ServerService) { }

  ngOnInit() {
    this.serverService.getAllStaffMembers()
      .subscribe((response: any) => {
        console.log(response._embedded);
        this.staff = response._embedded.staffs;
        this.formatStaffData();
      }, (error) => {
        console.log(error);
      });
  }

  formatStaffData() {
    for(let i = 0; i < this.staff.length; i++) {
      let date = new Date(this.staff[i].createDtm);
      this.staff[i].createDate = date.toDateString().substr(date.toDateString().indexOf(" "));
    }
  }

}



