import {AfterViewInit, Component, HostListener, OnDestroy, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {ServerService} from "../../../../services/server-service";
import {PartyService} from "../../../../services/party.service";
import {Subscription} from "rxjs/Subscription";
import {AppInteractionService} from "../../../../services/app-interaction.service";

@Component({
  selector: 'app-party-jobs',
  templateUrl: './party-jobs.component.html',
  styleUrls: ['./party-jobs.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PartyJobsComponent implements OnInit, OnDestroy, AfterViewInit {

  paramsReceived: string = null;
  partyJobs: any;
  partyServiceSubscription: Subscription;
  path: string;
  jobTypes = ['Service Request', 'Quote Request'];
  showDropdown: boolean = false;

  constructor(private activatedRoute: ActivatedRoute, private serverService: ServerService,
              private partyService: PartyService,
              private appInteractionService: AppInteractionService,
              private router: Router) {
  }

  @HostListener('document:keyup', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    console.log(event);
    if (event.keyCode === 27) {
      console.log('Escape!');
      this.showDropdown = false;
    }
  }

  @HostListener('document:click', ['$event'])
  hostClicked($event) {
    console.log($event.target.id);
    if ($event.target.id != 'dropdownMenuButton') {
      this.showDropdown = false;
    }
  }

  ngOnInit() {
    this.partyServiceSubscription = this.partyService.partySourceObservable
      .subscribe((data) => {
        if (data && data.id && data.type === PartyService.TYPE_JOBS) {
          this.paramsReceived = data.id;
          this.path = "http://localhost:8081/api/parties/" + this.paramsReceived + "/jobs";
          this.getUserJobs();
        }
      });

  }

  getUserJobs() {
    this.serverService.getRequestByPath(this.path)
      .subscribe(
        (jobs: any) => {
          console.log(jobs);
          this.partyJobs = jobs._embedded.jobs;
        },
        (error) => {
          console.log(error);
        });
  }

  ngAfterViewInit() {
    this.partyService.announceParentComponent({type: PartyService.TYPE_JOBS});
  }

  ngOnDestroy() {
    this.partyServiceSubscription.unsubscribe();
  }

  toggleDropdown() {
    this.showDropdown = !this.showDropdown;
    console.log(this.showDropdown);
  }

  onNewQuoteRequest() {
    console.log("navigate to new job page with quote request" + " params is " + this.paramsReceived);
    this.router.navigate(['jobs', AppInteractionService.QUOTE_REQUEST, "main"])
      .then((value) => {
          console.log(value);
        this.appInteractionService.requestApplicationComponent({
          id: AppInteractionService.QUOTE_REQUEST,
          type: AppInteractionService.TYPE_MAIN,
          partyId: this.paramsReceived
        });
        }
      ).catch((error) => {
      console.log(error);
    });

  }

  onNewServiceRequest() {
    console.log("navigate to new job page with quote request" + " params is " + this.paramsReceived);
    this.router.navigate(['jobs', AppInteractionService.SERVICE_REQUEST, "main"])
      .then((value) => {
          console.log(value);
          this.appInteractionService.requestApplicationComponent({
            id: AppInteractionService.SERVICE_REQUEST,
            type: AppInteractionService.TYPE_MAIN,
            partyId: this.paramsReceived
          });
        }
      ).catch((error) => {
      console.log(error);
    });
  }

}



