import {
  AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild,
  ViewEncapsulation
} from '@angular/core';
import {Subscription} from "rxjs/Subscription";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {ServerService} from "../../../../services/server-service";
import {Http} from "@angular/http";
import {ActivatedRoute, Router} from "@angular/router";
import {JobService} from "../../../../services/job.service";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";

@Component({
  selector: 'app-job-comments',
  templateUrl: './job-comments.component.html',
  styleUrls: ['./job-comments.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class JobCommentsComponent implements OnInit, AfterViewInit {
  userComments: any = null;
  newComment: boolean = false;
  userCommentInput: string;
  paramsReceived: any;
  jobServiceSubscription: Subscription;
  path: string = null;
  commentForm: FormGroup;
  uploadMessage = null;
  uploadingFile = false;
  commentUuid = null;
  uploadedFiles: {name:string, uuid:string}[] = [];
  readonly commentsUploadedFiles = ServerService.uploadedFilesUrl;

  @ViewChild("fileInput") fileInput;

  constructor(private serverService: ServerService, private http: Http,
              private activatedRoute: ActivatedRoute, private changeDetectorRef: ChangeDetectorRef,
              private  router: Router, private jobService: JobService,
              private ngbModal: NgbModal) {
  }

  ngOnInit() {
    this.jobServiceSubscription = this.jobService.jobSourceObservable
      .subscribe((data) => {
        if (data && data.id && data.type === JobService.TYPE_COMMENTS) {
          this.paramsReceived = data.id;
          this.path = "http://localhost:8081/api/jobs/" + this.paramsReceived + "/comments";
          this.getUserComments();
        }
      });
  }

  ngAfterViewInit() {
    this.jobService.announceParentComponent({type: JobService.TYPE_COMMENTS});
  }

  getUserComments() {
    this.serverService.getRequestByPath(this.path)
      .subscribe(
        (comments: any) => {
          console.log(comments);
          this.userComments = comments._embedded.comments;
          this.formatComments();
          this.getCommentsFiles();
        },
        (error) => {
          console.log(error);
        });
  }

  formatComments() {
    this.userComments.forEach((data) => {
      let date = data.commentDtm;
      let formattedDate = new Date(date);
      data.commentDtm = formattedDate.toDateString();
    });
    this.userComments.forEach((data) => {
      console.log(data);
      let path = data._links.self.href;
      let index = path.lastIndexOf("/") + 1;
      data.commentId = path.substring(index);
    });
  }

  getCommentsFiles() {
    this.userComments.forEach((comment)=>{
      this.serverService.getUploadedFilesFromCommentsId(comment.commentId).
      subscribe((response)=>{
        comment.uploadedFiles = response._embedded.uploadedFiles;
        console.log(comment.uploadedFiles);
      }, (error)=>{
        console.log(error);
      })
    });
  }




  createNewComment() {
    console.log("create new comment");
    this.createCommentForm();
    this.newComment = true;
  }

  createCommentForm() {
    this.commentForm = new FormGroup({
      'user-comment': new FormControl(null, [Validators.required, this.validateComment.bind(this)])
    });
  }

  validateComment() {
    console.log(this.commentForm);
    if (this.commentForm && this.commentForm.get('user-comment')
      && this.commentForm.get('user-comment').value
      && this.commentForm.get('user-comment').value.replace(/^\s+$/, '') != "") {
      return null;
    }
    else return {'Invalid Comment': true}
  }

  saveUserComment() {
    if (this.commentForm && this.commentForm.valid) {
      let comment: any = {};
      comment.comment = this.commentForm.get('user-comment').value;
      comment.staffUser = '/staffs/' + "motion6";
      comment.job = "/jobs" + '/' + this.paramsReceived;
      this.serverService.saveUserComment(comment)
        .subscribe((response) => {
          console.log(response);
          console.log(response.json());
          let path = (response.json())._links.self.href;
          let index = path.lastIndexOf("/") + 1;
          this.commentUuid = path.substring(index);
          if (this.uploadedFiles.length != 0) {
            let patchComment = {comment: "/comments/" + this.commentUuid};
            for(let i = 0; i < this.uploadedFiles.length; i++) {
              this.serverService.patchCommentWithUploadedFiles(this.uploadedFiles[i].uuid, patchComment)
                .subscribe((response: any) => {
                  console.log(response.json());
                }, (error) => {
                  this.ngbModal.open("Something went wrong, comment was saved without file "
                    + this.uploadedFiles[i].name );
                });
            }

          }
          else if (this.uploadedFiles.length == 0) {
            this.ngbModal.open("Comments Succesfull saved");
          }
          this.getUserComments();
          this.dontSaveUserComment();

        }, (error) => {
          console.log(error);
          this.ngbModal.open("Sorry, something went wrong. !");
        });
    }
    else {
      this.ngbModal.open("Please enter a valid comment");
    }
  }

  dontSaveUserComment() {
    this.commentForm.reset();
    this.newComment = false;
    this.uploadMessage = null;
  }

  usersCommentFileUpload($event) {
    console.log("usersCommentFileUpload called");
  }

  onSubmit() {
    if (this.commentForm.valid) {
      this.saveUserComment();
    }
    else {
      this.ngbModal.open("Please enter a valid comment");
    }
  }

  getUploadMessage() {
    return this.uploadMessage;
  }

  uploadFile() {
    let fileInput = this.fileInput.nativeElement;
    if (fileInput.files && fileInput.files[0]) {
      this.uploadMessage = null;
      this.uploadingFile = true;
      let file: File = fileInput.files[0];
      let formData: FormData = new FormData();
      formData.append('file', file, file.name);
      this.serverService.uploadFile(formData)
        .subscribe(
          (data) => {
            this.uploadMessage = "File successfully saved";
            let fileUuid = (data.json()).uuid;
            this.uploadedFiles.push({name:file.name, uuid:fileUuid});
            this.resetMessageStatus();
            console.log(data);

          },
          (error) => {
            this.uploadMessage = "Something went wrong, couldn't save the file !";
            this.resetMessageStatus();
            console.log(error);
          }
        );
    }
  }

  getFileUploadStatus() {
    return this.uploadingFile;
  }

  resetMessageStatus(timeout = 500) {
    setTimeout(() => {
      //this.uploadMessage = null;
      this.uploadingFile = false;
      this.fileInput.nativeElement.value = "";
    }, timeout);
  }
}



