import {AfterViewInit, Component, OnInit, ViewEncapsulation} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {ServerService} from "../../../services/server-service";
import {Location} from "@angular/common";
import {PartyService} from "../../../services/party.service";
import {Subscription} from "rxjs/Subscription";
import {AppInteractionService} from "../../../services/app-interaction.service";

@Component({
  selector: 'app-party',
  templateUrl: './party.component.html',
  styleUrls: ['./party.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [PartyService]
})
export class PartyComponent implements AfterViewInit, OnInit {

  readonly CREATE_CUSTOMER = AppInteractionService.CREATE_CUSTOMER;
  partyBills: any = null;
  partyInvoices: any = null;
  partyJobs: any = null;
  data: any;
  id: string = null;
  type: string = null;
  childComponentSubscription: Subscription;


  constructor(private activatedRoute: ActivatedRoute,
              private router: Router,
              private serverService: ServerService,
              private location: Location,
              private partyService: PartyService) {
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params) => {
      console.log(params['id']);
      this.id = params['id'];
      this.type = AppInteractionService.TYPE_MAIN;
      if (params['id'] != AppInteractionService.CREATE_CUSTOMER) {
        this.makeDataRequest();
      }
      this.announceToChildComponents();
    }, (error) => console.log(error));
    this.childComponentSubscription = this.partyService.childComponentObservable
      .subscribe((data) => {
        if (data && data.id) {
          this.id = data.id;
        }
        if (data && data.type) {
          this.type = data.type;
        }
        this.announceToChildComponents();
      }, (error) => {
        console.log(error);
      });

  }

  ngAfterViewInit() {

  }

  makeDataRequest() {
    this.makePartyRequest();
    this.makeJobsRequest();
    this.makeBillsRequest();
    this.makeInvoicesRequest();
    this.makeCommentsRequest();
  }

  makePartyRequest() {
    this.serverService.getRequestByPartyId(this.id, null, null)
      .subscribe((data: any) => {
        console.log(data);
        this.data = data;
      }, (error) => {
        console.log(error);
      });
  }

  makeCommentsRequest() {

  }

  makeJobsRequest() {
    this.serverService.getRequestByPartyId(this.id, '/jobs')
      .subscribe((jobs: any) => {
        this.partyJobs = jobs._embedded.jobs;
      }, (error) => {
        console.log(error);
      });
  }

  makeBillsRequest() {
    this.serverService.getRequestByPartyId(this.id, '/bills')
      .subscribe((bills: any) => {
        console.log(bills);
        this.partyBills = bills._embedded.bills;
      }, (error) => {
        console.log(error);
      });
  }

  makeInvoicesRequest() {
    this.serverService.getRequestByPartyId(this.id, '/invoices')
      .subscribe((invoices: any) => {
        if (invoices._embedded) {
          this.partyInvoices = invoices._embedded.invoices;
        }
        else {
          this.partyInvoices = null;
        }
      }, (error) => {
        console.log(error);
      });
  }

  announceToChildComponents() {
    this.partyService.announcePartyDetail({
      id: this.id,
      type: this.type
    });
  }

  onTabSelected() {
  }
}



