import {AfterViewInit, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {jobTabs} from "./job-tabs";
import {ActivatedRoute} from "@angular/router";
import {JobService} from "../../../services/job.service";
import {ServerService} from "../../../services/server-service";
import {Subscription} from "rxjs/Subscription";
import {AppInteractionService} from "../../../services/app-interaction.service";


@Component({
  selector: 'app-job',
  templateUrl: './job.component.html',
  styleUrls: ['./job.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [JobService]
})
export class JobComponent implements OnInit, AfterViewInit {

  jobTabs = jobTabs;
  id: string = null;
  type: string = null;
  jobId = null;
  childComponentSubscription: Subscription;
  appServiceSubscription: Subscription;
  data: any = null;
  partyId: any = null;
  quoteRequest = AppInteractionService.QUOTE_REQUEST;
  serviceRequest = AppInteractionService.SERVICE_REQUEST;


  constructor(private serverService: ServerService, private activatedRoute: ActivatedRoute,
              private jobService: JobService, private appInteractionService: AppInteractionService,) {
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params) => {
        console.log(params);
        this.id = params['id'];
        this.type = AppInteractionService.TYPE_MAIN;
        this.announceToChildComponents();
      }, (error) => {
        console.log(error)
      }
    );
    this.childComponentSubscription = this.jobService.childComponentObservable
      .subscribe((data) => {
        if (data && data.id) {
          this.id = data.id;
        }
        if (data && data.type) {
          this.type = data.type;
        }
        if (data && data.partyId) {
          this.partyId = data.partyId;
        }
        this.announceToChildComponents();
      }, (error) => {
        console.log(error);
      });
    this.appServiceSubscription = this.appInteractionService.appSourceObservable
      .subscribe((data) => {
        console.log(data);
        if (data && data.id) {
          this.id = data.id;
        }
        if (data && data.type) {
          this.type = data.type;
        }
        if (data && data.partyId) {
          this.partyId = data.partyId;
        }
      });
  }

  ngAfterViewInit() {
    this.appInteractionService.requestApplicationComponent(
      {id: this.id, type: this.type, partyId: this.partyId}
    );
  }

  announceToChildComponents() {
    console.log(this.id);
    console.log(this.type);
    console.log(this.partyId);
    this.jobService.announceJobDetail({
      id: this.id,
      type: this.type,
      partyId: this.partyId
    });
  }
}



