import {Directive, ElementRef, HostListener, OnInit,  Renderer2} from '@angular/core';

@Directive({
  selector: '[appFormInput]'
})
export class FormInputDirective implements OnInit{

  parentElement:any;

  constructor(private elementRef:ElementRef, private renderer:Renderer2) { }

  @HostListener('focus', ['$event'])
  onInputFocus($event) {
    this.renderer.addClass(this.parentElement, 'focus');
  }

  @HostListener('blur', ['$event'])
  onInputBlur($event) {
    this.renderer.removeClass(this.parentElement, 'focus');
  }

  ngOnInit() {
    this.parentElement = this.elementRef.nativeElement.parentElement;
    this.parentElement.addEventListener('click', ()=>{
      this.elementRef.nativeElement.focus();
    });
    this.renderer.listen(this.parentElement, 'click', ()=>{
      this.elementRef.nativeElement.focus();
    });
  }

}



