import { Component, OnInit } from '@angular/core';
import {Subscription} from "rxjs/Subscription";

import {ServerService} from "../../../../services/server-service";
import {NavBarService} from "../../../../services/nav-bar.service";
import {ActivatedRoute, Router} from "@angular/router";
import {JobService} from "../../../../services/job.service";

@Component({
  selector: 'app-job-bills',
  templateUrl: './job-bills.component.html',
  styleUrls: ['./job-bills.component.scss']
})
export class JobBillsComponent implements OnInit {
  paramsReceived: string = null;
  jobInvoices: any;
  rowData: any[];
  navBarSubscription: Subscription;
  jobServiceSubscription: Subscription;
  googleMapsClient: any;
  path: string = null;

  constructor(private serverService: ServerService,
              private navBarService: NavBarService,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private jobService: JobService) {

  }

  ngOnInit() {
    this.jobServiceSubscription = this.jobService.jobSourceObservable.subscribe((data) => {
      if (data && data.id && data.type === JobService.TYPE_BILLS) {
        this.paramsReceived = data.id;
        this.path = "http://localhost:8081/api/jobs/" + this.paramsReceived + "/bills";
        console.log(data);
        this.getBills();
      }
    }, (error) => console.log(error));

    this.navBarSubscription = this.navBarService.getNavBarSubject().subscribe((value) => {
      console.log("sizeColumnsToFit");
    });
  }

  getBills() {
    this.serverService.getRequestByPath(this.path)
      .subscribe((bills: any) => {
        console.log(bills);
        if (bills._embedded) {
          this.rowData = bills._embedded.bills;
          this.formatRowData();
        }
        else {
          this.rowData = null;
        }
      }, (error) => {
        console.log(error);
      });
  }
// @bill_ref | @party_name | @bill_date | @due_date <br>
// | @tax_inclusive | @total_amount | @total_sell_amount <br>

  formatRowData() {
    for (let i = 0; i < this.rowData.length; i++) {
      let startFullDate = new Date(this.rowData[i].billDt);
      let finishFullDate = new Date(this.rowData[i].dueDt);
      this.rowData[i].startDate = startFullDate.toDateString();
      this.rowData[i].startTime = startFullDate.toLocaleTimeString();
      this.rowData[i].finishDate = finishFullDate.toDateString();
      this.rowData[i].finishTime = finishFullDate.toLocaleTimeString();
    }
  }

  columnDefs = [
    {
      headerName: "Bill Ref",
      field: 'reference',
      cellClass: 'first-column app-table-cell'
    },
    {
      headerName: "Party Name",
      field: "customerName",
      cellClass: 'second-column app-table-cell'
    },
    {
      headerName: "Bill Date",
      field: "startDate",
      cellClass: 'third-column app-table-cell'
    },
    {
      headerName: "Due Date",
      field: "finishDate",
      cellClass: 'fourth-column app-table-cell'
    },
    {
      headerName: "Tax Inclusive",
      field: "taxInclusive",
      cellClass: 'fifth-column app-table-cell'
    },
    {
      headerName: "Total Amount",
      field: "totalAmount",
      cellClass: 'sixth-column app-table-cell'
    },
    {
      headerName: "Total Sell Amount",
      field: "totalSellAmountAmount",
      cellClass: 'seventh-column app-table-cell'
    }
  ];


  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onCellClicked($event) {
    console.log($event.data);
    let href = $event.data._links.self.href;
    let index = (href.lastIndexOf('/') + 1);
    let id = +(href.substr(index));
    console.log(id);
    // this.router.navigate(['job', id, 'bills'])
    //   .then((value) => {
    //     console.log(value);
    //   }).catch((error) => {
    //   console.log(error)
    // });
  }

  onCellDoubleClicked($event) {
    console.log($event.data._links.self);
  }

  ngAfterViewInit() {
    this.jobService.announceParentComponent({type: JobService.TYPE_BILLS});
  }

  ngOnDestroy() {
    this.navBarSubscription.unsubscribe();
    this.jobServiceSubscription.unsubscribe();
  }
}



