import {Injectable} from '@angular/core';
import {Http, RequestOptionsArgs, Response, Headers, RequestOptions} from '@angular/http';
import 'rxjs/Rx';

import {Observable} from "rxjs/Observable";
import {PartyModel} from "../models/party.model";


@Injectable()
export class ServerService {
  static readonly baseUrl = "http://localhost:8081";
  static readonly apiUrl = ServerService.baseUrl + "/api";
  static readonly partiesUrl = ServerService.apiUrl + "/parties";
  static readonly jobsUrl = ServerService.apiUrl + "/jobs";
  static readonly staffUrl = ServerService.apiUrl + "/staffs";
  static readonly uploadUrl = ServerService.apiUrl + "/upload";
  static readonly commentsUrl = ServerService.apiUrl + "/comments";
  static readonly uploadedFilesUrl = ServerService.apiUrl + "/uploadedFiles";
  static readonly jobStatusEntriesUrl = ServerService.apiUrl + "/jobStatusEntries"


  private baseUrl = ServerService.baseUrl;
  private apiUrl = ServerService.apiUrl;
  private partiesUrl = ServerService.partiesUrl;
  private jobsUrl = ServerService.jobsUrl;
  private staffUrl = ServerService.staffUrl;
  private uploadUrl = ServerService.uploadUrl;
  private commentsUrl = ServerService.commentsUrl;

  constructor(private http: Http) {
  }

  uploadFile(formData:FormData){
    let headers:Headers = new Headers();
    headers.append('Content-Type', 'multipart/form-data');
    let options = new RequestOptions(headers);
    return this.http.post(this.uploadUrl, formData, options);
  }

  getAllParties() {
    return this.http.get(this.partiesUrl)
      .map(
        (response: Response) => {
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }
  getAllJobs() {
    return this.http.get(this.jobsUrl)
      .map(
        (response: Response) => {
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }
  getAllStaffMembers() {
    return this.http.get(this.staffUrl)
      .map(
        (response: Response) => {
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }
  getRequestByJobId(Id) {
    return this.http.get(this.jobsUrl + "/" + Id)
      .map(
        (response: Response) => {
          console.log(response);
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }

  getRequestByPartyId(id: string, path?: string, reuquestOptionParams?: RequestOptionsArgs) {
    console.log(path);
    path = path ? path : '';
    return this.http.get(this.partiesUrl + "/" + id + path, reuquestOptionParams)
      .map(
        (response: Response) => {
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }

  getRequestByPath(path) {
    return this.http.get(path)
      .map(
        (response: Response) => {
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }

  //* var firstHeaders = new Headers();
  //* firstHeaders.append('Content-Type', 'image/jpeg');
  saveParty(party: PartyModel) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(ServerService.partiesUrl, party, {headers: headers});
  }

  patchParty(id, party: PartyModel) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.patch(ServerService.partiesUrl + '/' + id  , party, {headers: headers});
  }

  patchCommentWithUploadedFiles(fileUuid:string, comment:any,) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.patch(ServerService.uploadedFilesUrl + '/' + fileUuid,
        comment, {headers:headers});
  }

  putParty(id, party: PartyModel) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.put(ServerService.partiesUrl + '/' + id , party, {headers: headers});
  }

  saveJob(job:any) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(ServerService.jobsUrl, job, {headers: headers});
  }

  postJobStatus(jobStatus:any) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(ServerService.jobStatusEntriesUrl, jobStatus,
      {headers: headers});
  }

  putJob(id, job:any) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.put(ServerService.jobsUrl + '/' + id,job, {headers: headers});
  }

  saveUserComment(userComment) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(ServerService.commentsUrl,
      userComment, {headers:headers});
  }

  getUploadedFilesFromCommentsId(id:string) {
    return this.http.get(ServerService.commentsUrl + '/' + id + '/uploadedFiles')
      .map(
        (response: Response) => {
          console.log(response);
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }
}



